import React, { Component } from "react";
import productImage from "./logo512.png";

export class ProductList extends Component {
  render() {
    if (this.props.products == null || this.props.products.length === 0) {
      return <h5 className="p-2 no-products-text">No Products</h5>;
    }
    return this.props.products.map((p) => (
      <div className="card m-3 p-2 bg-light product-card" key={p.id}>
        <div className="add-to-cart-button-div">
          <h4 className="product-title">{p.name}</h4>
          <button
            className="btn btn-success add-to-cart-button"
            onClick={() => this.props.addToCart(p)}
          >
            Add To Cart
          </button>
        </div>
        <div className="category-name">
          <h4 className="category-name-title">Category:</h4>
          {p.category}
          <span className="badge badge-pill badge-primary float-right price-tag">
            ${p.price.toFixed(2)}
          </span>
        </div>

        <div className="card-text m-2 p-1 product-details">
          <div className="product-image-div">
            <img
              src={productImage}
              alt="product"
              className="product-image"
            ></img>
          </div>
          <div className="view-image-button-div">
            <button className="btn btn-success btn-sm float-right add-to-cart-button view-image-button">
              View Image
            </button>
          </div>
          <div className="product-description">
            <h3>Description</h3>{" "}
            <div className="product-description-text">{p.description}</div>
          </div>
        </div>
      </div>
    ));
  }
}
